		var timer
 // 验证密码 保存用户信息
	import { isPasswd, userHandle, checkTel } from '@/utils/myUtils.js'
	import {
		TOKEN,
		USERINFO
	} from '@/utils/storageTypes.js'
	export default {
		data() {
			return {
				sec: 59,
				disabled: true,
				isLogin: true,
				loginData: {
					phone: '',
					code: ''
				},
				registerData: {
					phone: '',
					code: '',
					password: ''
				}
			};
		},
		onLoad() {
			// 验证是否登录
			let res = this.$checkLogin()
			if (res) {
				console.log(4)
				// uni.switchTab({
				// 	url: '../device/index'
				// });
				uni.navigateTo({
					url: './startPage'
				})
			}
		},
		onUnload() {
			if (timer) {
				console.log('清楚了')
				clearInterval(timer)
			}
		},
		methods: {
			// 登录
			toLogin() {
				console.log('登录')
				if (!this.loginData.phone || !this.loginData.code) {
					this.toast('请将信息填写完整')
					return
				}
				if(!checkTel(this.loginData.phone)){
					this.toast('手机号错误')
					return
				}
			},
			// 获取验证码
			getCode() {
				// 1. 手机号验证
				if(!checkTel(this.loginData.phone)){
					this.toast('手机号错误')
					return
				}
				// 2. 调用接口 发验证码,成功后开始计时
				this.disabled = false
				timer = setInterval(()=>{
					console.log('发送中')
					if (this.sec) {
						this.sec -= 1
					} else {
						this.disabled = true
						this.sec = 59
						clearInterval(timer)
					}
				}, 1000);
				
			},
		}
	}
